<template>
  <div class="s_bg">
    <nav-bar class="s_nav">
      <i slot="left" class="el-icon-back" @click="back"></i>
      <div slot="center">
        <el-input
          placeholder="请输入标题"
          v-model="title"
          prefix-icon="el-icon-search"
          @clear="search"
          @keydown.enter.native="search"
          clearable></el-input>
      </div>
    </nav-bar>
    <!--    正文-->
    <div>
      <goods-list :goods="contentBody"/>
    </div>
  </div>
</template>

<script>
import NavBar from "@/components/navbar/NavBar";
import GoodsList from "../../components/goods/GoodsList";

export default {
  name: "Search",
  data() {
    return {
      title: '',
      contentBody: []
    }
  },
  methods: {
    back() {
      this.$router.go(-1)
    },
    search() {
      this.$http.get('article').then(res => {
          var n = 0;
          for (var i = 0; i < res.data.data.records.length; i++) {
            if (this.title === '') {
              this.contentBody = []
            } else if (res.data.data.records[i].title.includes(this.title) && this.title !== '') {
              this.contentBody[n] = res.data.data.records[i]
              n++;
            }
          }
          this.contentBody = Object.assign([], this.contentBody)
        }).catch(err => err)
    }
  },
  components: {
    GoodsList,
    NavBar
  }
}
</script>

